import React, {Component} from 'react';
import {Modal, Button} from 'antd';
import './index.css';

export default class AutoNumber extends Component{
		constructor(props) {
				super(props);
				this.owner = this.props.owner;
				this.state = {
					visible: true,
					numberNvisible: false,
					numberNMvisible: false,
					numberNYvisible: false,

					auto_name: '',
					auto_switch: false,
					auto_nuw: 0,
					auto_len: 0,
					auto_switch_m: false,
					auto_nuw_m: 0,
					auto_len_m: 0,
					auto_switch_y: false,
					auto_nuw_y: 0,
					auto_len_y: 0,
				}
				this.state = {...this.state, ...this.props};
		}

		handleOk(){
				this.props.setAuto(this.state.auto_name, this.state.auto_num, this.state.auto_len, this.state.auto_num_m, this.state.auto_len_m, this.state.auto_num_y, this.state.auto_len_y);
				this.handleCancel();
		}

		handleCancel(){
				window.setTimeout(()=>this.owner.setState({setShowFlag: false}), 0);
		}

		setAState(key, value){
				this.setState({[key]: value});
				if(key === 'auto_name'){
						if(value.indexOf('{N}') !== -1){
								this.setState({numberNvisible: true});
						}else{
							 this.setState({numberNvisible: false});
						}
						if(value.indexOf('{NM}') !== -1){
								this.setState({numberNMvisible: true});
						}else{
							 this.setState({numberNMvisible: false});
						}
						if(value.indexOf('{NY}') !== -1){
								this.setState({numberNYvisible: true});
						}else{
							 this.setState({numberNYvisible: false});
						}
				}
		}

		render(){
				return (
						<div>
								<Modal title="工作名称/文号" visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)} footer={[
										<Button key="back" type="ghost" size="large" onClick={this.handleCancel.bind(this)}>取 消</Button>,
          					<Button key="submit" type="primary" size="large" onClick={this.handleOk.bind(this)}>提 交</Button>
        				]} width={800} bodyStyle={{width:800,height:368,overflow:'auto'}} style={{top:20}}>
									    <div>
													<div className="main-container" style={{"margin-right":"20px;"}}>
															<label for="autoNumberExpr" className="target-table-lable">文号表达式</label>
															<input id="autoNumberExpr" value={this.state.auto_name} onChange={e => this.setAState('auto_name', e.target.value)}/>
															{
																this.state.numberNvisible ?
															<fieldset>
									    				<legend>N-编号</legend>
									            <label className="ant-checkbox-wrapper auto-number-lable-container">
									              <input type="checkbox" className="check-box" checked={this.state.auto_switch} onChange={e => this.setAState( 'auto_switch', e.target.checked)}/>
									              <span>应用此规则</span>
									            </label>
															<div className="auto-number-lable-container">
																<label for="autoNumberCount" className="auto-number-lable">计数器</label>
																<input id="autoNumberCount" style={{"width":"90px;"}} value={this.state.auto_nuw} onChange={e => this.setAState( 'auto_nuw', e.target.value)}/>
															</div>
															<div className="auto-number-lable-container">
																<label for="autoNumberNumTo" className="auto-number-lable">位数</label>
																<input id="autoNumberNumTo" style={{"width":"90px;"}} value={this.state.auto_len} onChange={e => this.setAState( 'auto_len', e.target.value)} />
															</div>
									  					</fieldset> : null
									  					}
									  				{
																this.state.numberNMvisible ?
									          <fieldset>
									    				<legend>NM-编号</legend>
									            <label className="ant-checkbox-wrapper auto-number-lable-container">
									              <input type="checkbox" className="check-box" checked={this.state.auto_switch_m} onChange={e => this.setAState('auto_switch_m', e.target.checked)}/>
									              <span>应用此规则</span>
									            </label>
															<div className="auto-number-lable-container">
																<label for="autoNumberCount" className="auto-number-lable">计数器</label>
																<input id="autoNumberCount" style={{"width":"90px;"}} value={this.state.auto_nuw_m} onChange={e => this.setAState('auto_nuw_m', e.target.value)}/>
															</div>
															<div className="auto-number-lable-container">
																<label for="autoNumberNumTo" className="auto-number-lable">位数</label>
																<input id="autoNumberNumTo" style={{"width":"90px;"}} value={this.state.auto_len_m} onChange={e => this.setAState('auto_len_m', e.target.value)} />
															</div>
									  				</fieldset> : null
									  				}
									  				{
																this.state.numberNYvisible ?
									          <fieldset v-show="numberNYvisible">
									    				<legend>NY-编号</legend>
									            <label className="ant-checkbox-wrapper auto-number-lable-container">
									              <input type="checkbox" className="check-box" checked={this.state.auto_switch_y} onChange={e => this.setAState('auto_switch_y', e.target.checked)}/>
									              <span>应用此规则</span>
									            </label>
															<div className="auto-number-lable-container">
																<label for="autoNumberCount" className="auto-number-lable">计数器</label>
																<input id="autoNumberCount" style={{"width":"90px;"}} value={this.state.auto_nuw_y} onChange={e => this.setAState('auto_nuw_y', e.target.value)} />
															</div>
															<div className="auto-number-lable-container">
																<label for="autoNumberNumTo" className="auto-number-lable">位数</label>
																<input id="autoNumberNumTo" style={{"width":"90px;"}} value={this.state.auto_len_y} onChange={e => this.setAState('auto_len_y', e.target.value)} />
															</div>
									  					</fieldset> : null
									  				}
														</div>
														<div className="main-container declareTable">
															<p className="declareTitle">一、文号表达式说明</p>
															<p>表达式中可以使用以下特殊标记：</p>
															<p><span>{'{Y}'} ：表示年</span><span style={{"margin-left": "98px;"}}>{'{M}'} ：表示月</span><span style={{"margin-left": "70px;"}}>{'{D}'} ：表示日</span></p>
															<p><span>{'{H}'} ：表示时</span><span style={{"margin-left": "97px;"}}>{'{I}'} ：表示分</span><span style={{"margin-left": "70px;"}}>{'{S}'} ：表示秒</span></p>
															<p><span>{'{U}'} ：表示用户姓名</span><span style={{"margin-left": "62px;"}}>{'{R}'} ：表示角色</span></p>
															<p><span>{'{SD}'} ：表示短部门</span></p>
															<p><span>{'{LD}'}：表示长部门</span></p>
															<p>{'{N}'} ：表示编号，通过编号计数器取值并自动增加计数值</p>
															<p>{'{NY}'} ：表示编号，每过一年编号重置一次</p>
															<p>{'{NM}'} ：表示编号，每过一月编号重置一次</p>
															<p>{'例如，表达式为：成建委发[{Y}]{N}号，同时设置自动编号显示长度为4，'}</p>
															<p>则自动生成的文号如下：成建委发[2006]0001号。</p>
															<p>{'例如，表达式为：BH{N}，同时设置自动编号显示长度为3，'}</p>
															<p>则自动生成的文号如下：BH001。</p>
															<p>可以不填写自动文号表达式，则系统默认按以下格式，如：</p>
															<p>请假流程(2006-01-01 10:30:30)。</p>
															<p className="declareTitle">二、编号计数器说明</p>
															<p>用于表达式编号标记，当前编号值</p>
															<p className="declareTitle">三、编号位数说明</p>
															<p>为0表示按实际编号位数显示</p>
									                        <p className="declareTitle">四、控件表达式说明</p>
															<p style={{'width':'500px'}}>{'可选择表单中的单行文本、数字文本、货币文本、单选框、下拉框、复选框、人员选择、部门选择控件作字段引用表达式，如：{单行文本}，最多可选择三个控件进行添加。'}</p>
														</div>
									    </div>
        				</Modal>
        		</div>
        )
		}
}